import { render, screen } from '@testing-library/react'
import ModuleHeader from '../../components/common/ModuleHeader'

describe('ModuleHeader', () => {
  const defaultProps = {
    title: 'Test Module',
    subtitle: 'Test module description',
    icon: 'üçΩÔ∏è'
  }

  it('renders the title', () => {
    render(<ModuleHeader {...defaultProps} />)
    
    const title = screen.getByText('Test Module')
    expect(title).toBeInTheDocument()
  })

  it('renders the subtitle when provided', () => {
    render(<ModuleHeader {...defaultProps} />)
    
    const subtitle = screen.getByText('Test module description')
    expect(subtitle).toBeInTheDocument()
  })

  it('renders the icon when provided', () => {
    render(<ModuleHeader {...defaultProps} />)
    
    // Icon'u ayrƒ± aramak yerine title ile birlikte kontrol ediyoruz
    const titleWithIcon = screen.getByText('Test Module')
    expect(titleWithIcon).toBeInTheDocument()
    
    // Icon'un varlƒ±ƒüƒ±nƒ± kontrol etmek i√ßin parent element'i kontrol edelim
    const titleContainer = titleWithIcon.closest('div')
    expect(titleContainer?.textContent).toContain('üçΩÔ∏è')
  })

  it('does not render subtitle when not provided', () => {
    render(<ModuleHeader title="Test Module" icon="üçΩÔ∏è" />)
    
    const subtitle = screen.queryByText('Test module description')
    expect(subtitle).not.toBeInTheDocument()
  })

  it('renders children when provided', () => {
    render(
      <ModuleHeader {...defaultProps}>
        <button>Test Button</button>
      </ModuleHeader>
    )
    
    const button = screen.getByRole('button', { name: 'Test Button' })
    expect(button).toBeInTheDocument()
  })

  it('renders without icon when not provided', () => {
    render(<ModuleHeader title="Test Module" subtitle="Test description" />)
    
    const titleElement = screen.getByText('Test Module')
    expect(titleElement).toBeInTheDocument()
    
    // Icon olmamalƒ±
    const iconElement = screen.queryByText('üçΩÔ∏è')
    expect(iconElement).not.toBeInTheDocument()
  })
}) 